* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*::-webkit-scrollbar {
    /* height: 1px; */
    width: 3px;
}

*::-webkit-scrollbar-thumb {
    background-color: #ffffff;
    border-radius: 15px;
}

*::-webkit-scrollbar-track {
    background-color: #1f1d1d;
}

html{
    scroll-behavior: smooth;
}

body {
    overflow-x: hidden;
    width: 100vw;
    font-family: 'Montserrat', sans-serif;
}

h1{
    font-size: 65px;
}

h2{
    font-size: 30px;
    font-weight: 500;
}

h3{
    font-size: 33px;
    font-weight: 400;
}

h4{
    font-size: 14px;
    font-weight: 300;
}

.main{
    overflow-x: hidden;
}